    display: none;
  }
  .enter-your-personal-detalis-to {
    padding-left: 107px;
    padding-right: var(--padding-94xl);
    box-sizing: border-box;
  }
  .luna {
    font-size: 32px;
    line-height: 38px;
  }
  .divframerfjvu {
    padding-left: 115px;
    padding-right: var(--padding-94xl);
    box-sizing: border-box;
  }
  .h1,
  .h11,
  .h12,
  .h13,
  .h14 {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .wrapper {
    flex: 1;
    min-width: 100%;
  }
  .frame-parent {
    flex-wrap: wrap;
    gap: 0 43px;
  }
  .feature-section {
    gap: 42px 0;
  }
  .menu-frame {
    gap: 100px 0;
  }
}
@media screen and (max-width: 450px) {
  .enter-your-personal-detalis-to {
    display: none;
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
  .nav {
    width: 0;
  }
  .luna {
    font-size: 24px;
    line-height: 29px;
  }
  .h1,
  .h11,
  .h12,
  .h13 {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .menu-frame1 {
    flex-wrap: wrap;
  }
  .frame-look-at-menu-book-table {
    gap: 42px 0;
  }
  .h14 {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .map-container {
    padding-left: var(--padding-xl);
    box-sizing: border-box;
  }
}
